@use 'base/variables' as v;
@use 'base/mixins' as m;

.bloques {
	&__grid {
		@include m.tablet {
			@include m.grid(2, 0rem);
			grid-auto-flow: dense;
		}
	}
}
.bloque {
	height: 40rem;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	
	&:nth-child(1) {
		background-color: v.$primario;
	}
	&:nth-child(2) {
		@include m.bgImage('../img/bloque_1.jpg');
	}
	&:nth-child(3) {
		background-color: v.$secundario;
		@include m.tablet {
			grid-column: 2 / 3;
		}
	}
	&:nth-child(4) {
		@include m.bgImage('../img/bloque_2.jpg');
	}

	&__contenido {
		max-width: 25rem;
		border-block: 1px solid v.$blanco; //equivale a border-top y border-bottom
		color: v.$blanco;
		padding: 1rem 0;

		@include m.telefonoL {
			max-width: 35rem;
		}

		@include m.tablet {
			max-width: 30rem;
		}

		@include m.tabletL {
			max-width: 40rem;
		}
	}
	&__heading {
		text-transform: uppercase;
	}
	&__texto {
		font-size: 2rem;
	}
}