@use 'base/variables' as v;
@use 'base/mixins' as m;

.nosotros {
	padding: 5rem 0;	
	&__heading {
		@include m.heading;
	}
	&__grid {
		@include m.tablet {
			@include m.grid(3, 2rem);
		}
		@include m.desktopXL {
			@include m.grid(3, 10rem);
		}
	}
	&__bloque {
		margin-bottom: 4rem;
		text-align: center;
		&:last-of-type {
			margin-bottom: 0;
		}
		@include m.tablet {
			margin-bottom: 0;
		}
	}
	&__imagen {
		margin: 0 auto;
	}
}