@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
	padding: 3rem 0;
	border-bottom: .5rem solid v.$primario;
	&__grid {
		@include m.desktop {
			display: flex;
			justify-content: space-between;
		}
	}
	&__navegacion {
		justify-content: center;
	}
	&__copyright {
		text-align: center;
		line-height: 1;
		margin: 4rem 0 0 0;
		@include m.telefonoL {
			font-size: 2.4rem;
		}
		@include m.tablet {
			margin: 3rem 0 0 0;
		}
		@include m.desktop {
			margin: 0;
		}
	}
}