@use 'base/variables' as v;
@use 'base/mixins' as m;

.clases {
	padding: 5rem 0;
	background-color: v.$gris;
	&__heading {
		@include m.heading;
	}
	&__grid {
		@include m.tablet {
			@include m.grid(2, 2rem);
			align-items: start;
		}
		@include m.desktop {
			@include m.grid(3, 2rem);
		}
	}
}
.clase {
	border: 1px solid darken(v.$gris, 10%);
	background-color: v.$blanco;
	margin-bottom: 2rem;
	@include m.tablet {
		margin-bottom: 0;
	}
	&__imagen {
		width: 100%;
	}
	&__meta {
		background-color: v.$primario;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 1rem;
		color: v.$blanco;
	}
	&__precio {
		background-color: v.$secundario;
		padding: .5rem;
		font-size: 1.2rem;
		font-weight: 700;
		color: v.$negro;
		border-radius: 1rem;
	}
	&__contenido {
		padding: 2rem;
	}
	&__nombre {
		@include m.tablet {
			height: 5.5rem;
			overflow: hidden;			
		}
	}
	&__descripcion {
		color: v.$primario;
		@include m.tablet {
			height: 5.5rem;
			overflow: hidden;
		}
	}
	&__enlace {
		@include m.boton(v.$primario, v.$blanco);
		display: block;
		font-size: 1.6rem;
		padding: 1rem;
	}
}